import { runHandlers } from './handlers';

export default function loadRoom(datasource) {
  let room = window.User.rooms[0];

  //    ()
  let facilities = ['r_building_id', //  
    'r_improve_water_id', // 
    'r_improve_toilet_id', // 
    'r_toilet_other_households', // 
    'r_improve_bath_id', //  / 
    'r_improve_bathhouse_id', //  / 
    'r_improve_hotwater_id', //  
    'r_improve_sewage_id', //  ()
    'r_improve_kitchen_id', //    
    'r_improve_heating_id', // 
    'r_improve_electricity_id', // 
    'r_improve_electric_stove_id', //   
    'r_improve_gas_id', // 
    'r_improve_garbage_id', //   
    'r_period_id', //   
    'r_composition_id' //    
  ];
  console.log(datasource);
  for (let i = 0; i < facilities.length; i++) {
    if ( datasource === 0 && i === 0 ) {
      console.log(datasource === 0);
      console.log(datasource);
      document.querySelectorAll('[name='+facilities[i]+']').forEach(el => {
        el.parentElement.style.pointerEvents = 'none';
        if ( +el.value === 13 ) {
          el.checked = true;
        } else {
          el.checked = false;
        }
	    });
    } else {
      if (room[facilities[i]] === '99') {
        document.querySelectorAll('[name='+facilities[i]+']').forEach(el => {
          el.parentElement.style.pointerEvents = 'auto';
          el.checked = false;
        });
      } else {
        if ( window.User.rooms[0].r_datasource !== 0 ) {
          document.querySelectorAll('[name='+facilities[i]+']').forEach(el => {
            el.parentElement.style.pointerEvents = 'auto';
          });
        }
        document.querySelector('[name='+facilities[i]+'][value="'+room[facilities[i]]+'"]').checked = true;
      }
    }
	  
  } // for(let i=0; i<facilities.length; i++)

  //     
  if (+room.r_building_id === 11) { //   , etc.
	  document.querySelector('[name=r_rolling_stock]').value = room.r_rolling_stock;
  } else {
	  document.querySelector('[name=r_rolling_stock]').value = '';
  }
  if (+room.r_building_id === 12) { //   
	  document.querySelector('[name=r_build_type_another]').value = room.r_build_type_another;
  } else {
	  document.querySelector('[name=r_build_type_another]').value = '';
  }

  //  
  document.querySelector('[name=r_total_area]').value = room.r_total_area;

  //     
  if (+room.r_improve_garbage_id === 65) { //   
	  document.querySelector('[name=r_garbage_another]').value = room.r_garbage_another;
  } else {
	  document.querySelector('[name=r_garbage_another]').value = '';
  }

  //     
  if (+room.r_composition_id === 6) { //   
	  document.querySelector('[name=r_composition_another]').value = room.r_composition_another;
  } else {
	  document.querySelector('[name=r_composition_another]').value = '';
  }

  //   workflow

  runHandlers();
}
