import openPopupCheck from '../popup/popup-check';

export default function checkTabData() {

  return new Promise(function(resolve, reject) {
    //      root
    let sectionName = document.querySelector( '.js-root' ).dataset.section;
    //   
    let tabsTitle = document.querySelectorAll( '[href^="#' + sectionName + '"]' );
    let currentTab;
    //      family
    let currentTabFamily = document.querySelector('.js-members li[data].is-active').getAttribute('data');

    //     family    ,   
    if ( sectionName === 'family' && currentTabFamily === 'tree' ) {
      console.log('bla');
      return resolve( 0 );
    }

    //   
    tabsTitle.forEach(link => {
      if ( link.parentElement.classList.contains('is-active') ) {
        currentTab = link.parentElement;
      }
    });

    if ( currentTab !== undefined ) {
      //     href   
      let chunks = currentTab.firstChild.href.split( /#/ );
      let regex = new RegExp( sectionName + '-' );
      let questionNumber = chunks[1].replace(regex, '');

      //   .
      let checks;
      switch(sectionName) {
        case 'room':
          checks = util.checkRoom;
          break;
        case 's_member':
          checks = util.checkMember;
          break;
        case 'family':
          checks = util.checkHousehold;
          break;
        default:
          checks = [];
      }

      if ( checks.length ) {
        //    checkName ?
        if ( typeof checks[questionNumber] === 'function' ) {
          let res = checks[questionNumber](); //  
          //   
          if ( res['code'] === 0 ) { //  
            resolve( res['code'] );
          } else {
            //  popup
            openPopupCheck(res['code'], res['text'], resolve, reject);
          } // if(res['code'] === 0) ... else
        } 
      } else {
        let res = checks();
        //   
        if ( res['code'] === 0 ) { //  
          resolve( res['code'] );
        } else {
          //  popup
          openPopupCheck(res['code'], res['text'], resolve, reject);
        } 
      }
      

    } else { //   ,     
      resolve( 0 );
    }
    
    

    //     / ( progress line ) 
    if ( document.querySelector( '.js-root' ).getAttribute('data-section') === 's_member' ) {
      util.updateProgressMemberInForm();
    }

  }); // new Promise

}
