import openPopupWarn from '../popup/popup-warn';
//import generateName from './generate-name';

export default function checkHouseholdDel( num ) {
  return new Promise((resolve, reject) => {
    let curNum = window.User.rooms[0].households.length;
    if(num < curNum) { //    

      let name = [];
      for(let i=num; i<curNum; i++) {
        if( window.User.rooms[0].households[i].h_id ) {
          // id  , ,     
          name.push( window.User.rooms[0].households[i].h_name );
        }
      } // for(let i=(curNum-1); i>=num; i--)

      if(name.length > 0) {
        openPopupWarn('<span class="popup-check__subtitle">     :</span><ol><li>' +
          name.join('</li><li>') + '</li></ol><span class="popup-check__subtitle"> ?</span>', resolve, reject);
      } else { //  "" ,     
        resolve( 0 );
      }

    } else { // if(num < curNum)
      //    
      resolve( 0 );
    }

  }); // new Promise
}
