import generateName from './generate-name';

export default function checkHouseholdObj() {
  let h = window.User.rooms[0].households;
  let warning = false;
  let error = false;
  let res;
  let buf = [];

  for ( let i = 0; i < h.length; i++ ) {
    res = checkOne( h[i] );
    buf.push( {title: generateName( h[i].h_household_number ), content: res.content, id: h[i].h_id } );
  }

  return buf;
}

function checkOne( obj ) {
  let allErrors = [];
  const h_internet = +obj.h_improve_internet_id;
  const h_ownership = +obj.h_ownership_id;
  const householdRooms = obj.h_household_rooms;
  const tempResidents = obj.h_temp_residents;
  const roomPart = h_household_room_parts;
  console.log(obj);
  if ( +obj.h_household_member_count !== obj.members.length ) {
    allErrors.push({
      code: 2, 
      text: `    (${obj.members.length})      <b>15. ( )</b>    (${obj.h_household_member_count})`,
      number: 15});
  }

  if ( obj.h_id === null ) { //   
    allErrors.push({code: 1, text: '      ', number: 100});
    allErrors.push({code: 1, text: '     ', number: 100});
    allErrors.push({code: 1, text: '      ', number: 100});
    return {content: allErrors};
  }

  //   
  if ( obj.h_name === null || obj.h_name.trim() === '' ) {
    obj.h_name = generateName( obj.h_household_number );
  }

  //   
  if ( !tempResidents && (+window.User.rooms[0].r_building_id <= 8 || +window.User.rooms[0].r_building_id === 99) ) {

    if ( h_ownership === 99 ) {
      allErrors.push({code: 1, text: '      ', number: 100});
    }

    if ( !householdRooms && !tempResidents && !roomPart ) { //    
      allErrors.push({code: 1, text: '     ', number: 100});
    }

    if ( h_internet === 99 && !tempResidents) { //      
      allErrors.push({code: 1, text: '      ', number: 100});
    }
    if ( !allErrors.length ) {
      allErrors.push({code: 0, text: 'Ok', number: 100});
    }
    
    return {content: allErrors}; //      
  }

  //   
  if ( h_internet === 99 && !tempResidents) {
    allErrors.push({code: 1, text: '      ', number: 100});
    return {content: allErrors}; //      
  }
  
  allErrors.push({code: 0, text: 'Ok', number: 100});
  return {content: allErrors};
}
