//  2 -  
function handler2() {
  const input = document.querySelector('input[name=r_total_area]');
  let str = input.value;

  //  -   
  let r = str.replace(/([^\d]*)/g, '');
  if ( r.charAt( 0 ) === '0' ) r = r.slice( 1 );
  input.value = r;
}

//  3 - 
function handler3() {
  const input = document.querySelector('input:checked[name=r_improve_water_id]');
  const inputs = document.querySelectorAll('input[name=r_improve_bath_id]');
  const waterSupplyAbsent = 6;
  let val;
  const inputBuilding = document.querySelector('input:checked[name=r_building_id]');
  let valBuilding;
  
  if (inputBuilding === null) { //   
	  valBuilding = 0;
  } else {
	  valBuilding = +inputBuilding.value;
  }


  //     
  if ( input === null ) {
    val = 0;
  } else {
    val = +input.value;
  }
  
  //     ()  (5)
  if ( valBuilding > 8 ) {

    //    () 
    document.querySelectorAll('input[name=r_improve_bath_id]').forEach(el => {
	    el.disabled = true;
	    el.checked = false;
    });
    //  , 
    document.querySelectorAll('input[name=r_improve_bathhouse_id]').forEach(el => {
	    el.disabled = true;
	    el.checked = false;
    });

    document.querySelector("a[href='#room-5']").parentElement.classList.add('disabled');
    document.querySelector("a[href='#room-5']").parentElement.classList.remove('ok', 'warning', 'error');
  } else {

    document.querySelectorAll('input[name=r_improve_bath_id]').forEach(el => {
	    el.disabled = false;
    });

    document.querySelectorAll('input[name=r_improve_bathhouse_id]').forEach(el => {
	    el.disabled = false;
    });

    document.querySelector("a[href='#room-5']").parentElement.classList.remove('disabled');
  }

  //   ,  "  "   ()  (5)
  if ( val === waterSupplyAbsent ) {
    for ( let i = 0; i < inputs.length; i++ ) {
      console.log(+inputs[i].value);
      if ( +inputs[i].value === 19 ) {
        inputs[i].disabled = true;
        inputs[i].checked = false;
        inputs[i].parentElement.classList.add('is-disabled');
        break;
      }
    };
  } else {
    for ( let i = 0; i < inputs.length; i++ ) {
      if ( +inputs[i].value === 19 ) {
        inputs[i].disabled = false;
        inputs[i].parentElement.classList.remove('is-disabled');
        break;
      }
    }
  }
}


//  4 - 
function handler4() {
  const input = document.querySelector('input:checked[name=r_improve_toilet_id]');
  let val;

  //     
  if ( input === null ) {
    val = 0;
  } else {
    val = +input.value;
  }

  if ( val === 11) {
    document.querySelectorAll('input[name=r_toilet_other_households]').forEach(el => {
      el.disabled = false;
      el.parentElement.classList.remove( 'disabled' );
      el.parentElement.parentElement.parentElement.classList.remove( 'is-disabled' );
    });
  } else {
    document.querySelectorAll('input[name=r_toilet_other_households]').forEach(el => {
      el.checked = false;
      el.disabled = true;
      el.parentElement.classList.add( 'disabled' );
      el.parentElement.parentElement.parentElement.classList.add( 'is-disabled' );
    });
  }
}

