import { quantitiGrandFatherAndMother, disabledGrandFatherAndMother } from './helpers-form-create-member';
import initSelectChildren from './create-select-children';

export default function openEditFormCreateMember() {
  const currentForm = document.querySelector('.js-form-create-member');
  const btnCurrentForm = currentForm.querySelector('button[type="submit"]');
  const popupMember = this.parentElement.parentElement;
  const fieldsGender = currentForm.querySelectorAll('[name="m_gender_id"]');
  const fieldBirthDay = currentForm.querySelector('[name="m_birth_day"]');
  const filedBirthMonth = currentForm.querySelector('[name="m_birth_month"]');
  const fieldBirthYear = currentForm.querySelector('[name="m_birth_year"]');
  const fieldConnectionToFirst = currentForm.querySelector('[name="m_sib_id"]');
  const fieldSpouse = currentForm.querySelector('[name="m_spouse_id"]');
  const fieldParent = currentForm.querySelector('[name="m_parent_id"]');
  const fieldName = currentForm.querySelector('[name="m_name"]');
  const fieldChildren = currentForm.querySelector('[data-select-children-multi="m_children"]');

  const optionTemplate = document.createElement('option');
  const memberID = popupMember.getAttribute('member-id');
  
  const firstCode = '1'; //   /
  const wifeOrHusbandCode = '2';
  const parentCode = '4';
  const childrenCode = '3';

  let haveWifeOrHusband = false;
  let parentsId = [];
  let currentMember;
  let firstMember;

  members.forEach(member => {
    if ( member.is_first ) {
      firstMember = member;
    }
  });

  fieldChildren.parentElement.parentElement.classList.remove('is-disabled');
  //  ,    ,           
  currentForm.setAttribute('first-time', false);
  
  members.forEach(member => {
    //     /
    if ( member.m_sib_id === wifeOrHusbandCode ) {
      haveWifeOrHusband = true;
    } 

    //   id   /
    if ( member.m_id === memberID) {
      currentMember = member;
    }

    //    id   
    if ( member.m_sib_id === parentCode ) {
      parentsId.push(member.m_id);
    }

  });

  //  
  fieldBirthDay.innerHTML = '';
  for ( let i = 1; i < util.maxDays[currentMember.m_birth_month - 1] + 1; i++ ) {
    let option = optionTemplate.cloneNode();
    option.setAttribute('value', i);
    option.innerText = i;
    fieldBirthDay.appendChild(option);
  }

  [...fieldConnectionToFirst.children].forEach(option => {
    //     
    if ( option.value === firstCode ) {
      option.disabled = true;
    }
    //   /  
    if ( option.value === wifeOrHusbandCode && (haveWifeOrHusband || +firstMember.m_gender_id === +currentMember.m_gender_id ) ) {
      option.disabled = true;
    //   /
    } else if ( option.value === wifeOrHusbandCode && !haveWifeOrHusband ) {
      option.disabled = false;
    }
  });

  popupMember.classList.remove('is-show');
  currentForm.classList.add('is-edit');
  btnCurrentForm.innerText = '';
  btnCurrentForm.setAttribute('member-id', currentMember.m_id);
  

  for ( let key in currentMember ) {
    const notFirst = currentMember.is_first !== true;
    const itsGender = key === 'm_gender_id';
    const itsName = key === 'm_name';
    const itsAge = key === 'm_birth_month' || key === 'm_birth_day' || key === 'm_birth_year';
    const itsConnectionToFirst = key === 'm_sib_id';
    const itsMarriage = key === 'm_spouse_id';
    const itsParent = key === 'm_parent_id';
    const itsConnectionForm = itsGender || itsName || itsAge || itsConnectionToFirst || itsMarriage || itsParent;
    
    //       
    if ( itsConnectionForm ) {
     
      let currentElement = currentForm.querySelector(`[name="${key}"]`);
      let itsInputName = currentElement.type === 'text';
      let itsSelectAge = currentElement.getAttribute('data') === 'age';
      let itsConnectionToFirst = currentElement.name === 'm_sib_id';
      // let itsInputAge = currentElement.name === 'm_age';

      //     -  NodeList
      if ( currentElement !== null && currentElement.type === 'radio' ) {
        currentElement = currentForm.querySelectorAll(`[name="${key}"]`);
      }


      if ( currentElement !== null ) {
        //   NodeList -   ,  
        if ( NodeList.prototype.isPrototypeOf(currentElement) ) {
          currentElement.forEach(option => {
            if ( option.value === currentMember[key] ) {
              option.checked = true;
            }
          });
          //     -  
        } else if ( itsSelectAge && currentMember[key] !== null || itsInputName ) {
          currentElement.value = currentMember[key];
          //     ```   ``` -  
        } else if ( itsConnectionToFirst ) {
          //          
          [...currentElement.children].forEach(option => {
            option.innerText = option.getAttribute(`${currentMember.m_gender_id}`);
          });
          currentElement.value = currentMember[key];
        }
      }
      
      //   /
      if ( currentElement.name === 'm_parent_id' ) {

        members.forEach(member => {
          if ( member !== currentMember && +member.m_age > +currentMember.m_age && member.m_id !== currentMember.m_spouse_id ) {

            if ( currentMember.is_first && parentsId.length ) {

              parentsId.forEach(parent => {
                if ( member.m_id === parent ) {
                  let option = new Option();
                  option.innerText = `${member.m_name} (${member.m_age}.)`;
                  option.value = member.m_id;
                  currentElement.appendChild(option);
                }
              });

            } else {

              if ( currentMember.m_sib_id !== childrenCode ) {
                if ( !member.is_first ) {
                  let option = new Option();
                  option.innerText = `${member.m_name} (${member.m_age}.)`;
                  option.value = member.m_id;
                  currentElement.appendChild(option);
                }
              } else {
                if ( member.is_first || member.m_sib_id === wifeOrHusbandCode ) {
                  let option = new Option();
                  option.innerText = `${member.m_name} (${member.m_age}.)`;
                  option.value = member.m_id;
                  currentElement.appendChild(option);
                }
              }
            }
            

          }
        });

        if ( itsParent && currentMember[key] !== null ) {
          currentElement.value = currentMember[key];
        }

      }

      //   /
      if ( currentElement.name === 'm_spouse_id' ) {
        members.forEach(member => {
          //     
          if ( member !== currentMember && member.m_gender_id !== currentMember.m_gender_id && member.m_sib_id !== wifeOrHusbandCode && !currentMember.is_first ) {
            let option = new Option();
            option.innerText = `${member.m_name} (${member.m_age}.)`;
            option.value = member.m_id;
            currentElement.appendChild(option);
            if ( option.value === member.m_id && member.is_first ) {
              option.disabled = true;
            }
          }
          //   
          if ( member !== currentMember && member.m_gender_id !== currentMember.m_gender_id && currentMember.is_first ) {
            let option = new Option();
            option.innerText = `${member.m_name} (${member.m_age}.)`;
            option.value = member.m_id;
            currentElement.appendChild(option);
            if ( option.value === member.m_id && member.is_first ) {
              option.disabled = true;
            }
          }
        });

        if ( itsMarriage && currentMember[key] !== null ) {;
          currentElement.value = currentMember[key];
        }
      }
    }
  }

  //    ,     
  if ( currentMember.is_first ) {
    currentForm.classList.add('is-first');
    fieldConnectionToFirst.disabled = true;
    fieldConnectionToFirst.parentElement.parentElement.classList.add('is-disabled');
  } else {
    fieldConnectionToFirst.disabled = false;
    fieldConnectionToFirst.parentElement.parentElement.classList.remove('is-disabled');
  }

  //  / (. 2 - , 2 - )
  quantitiGrandFatherAndMother();
  disabledGrandFatherAndMother();

  //  is-empty   
  if ( fieldParent.value === '' ) {
    fieldParent.classList.add('is-empty');
  } else {
    fieldParent.classList.remove('is-empty');
  }
  if ( fieldSpouse.value === '' ) {
    fieldSpouse.classList.add('is-empty');
  } else {
    fieldSpouse.classList.remove('is-empty');
  }
  if ( fieldConnectionToFirst.value === '' ) {
    fieldConnectionToFirst.classList.add('is-empty');
  } else {
    fieldConnectionToFirst.classList.remove('is-empty');
  }

  //      
  fieldSpouse.disabled = false;
  fieldSpouse.parentElement.parentElement.classList.remove('is-disabled');
  fieldParent.disabled = false;
  fieldParent.parentElement.parentElement.classList.remove('is-disabled');
  fieldName.disabled = false;
  fieldName.classList.remove('is-disabled');
  fieldName.parentElement.parentElement.classList.remove('is-disabled');

  //      (      )
  filedBirthMonth.classList.add('is-disabled');
  filedBirthMonth.disabled = true;
  fieldBirthDay.classList.add('is-disabled');
  fieldBirthDay.disabled = true;
  fieldBirthYear.classList.add('is-disabled');
  fieldBirthYear.disabled = true;
  fieldBirthYear.parentElement.parentElement.parentElement.classList.add('is-disabled');
  // currentForm.querySelector('[name="m_bith_forgot"]').parentElement.parentElement.parentElement.classList.add('is-disabled');
  // currentForm.querySelector('[name="m_age"]').parentElement.parentElement.classList.add('is-disabled');
  // currentForm.querySelector('[name="m_age"]').disabled = true;
  fieldsGender.forEach(radio => {
    radio.parentElement.classList.add('is-disabled');
  });

  //     
  currentForm.querySelector('.js-member-age').innerText = currentMember.m_age + ' .';

  //    /    ,    /
  if ( currentMember.m_sib_id === wifeOrHusbandCode || currentMember.is_first ) {
    fieldSpouse.disabled = true;
    fieldSpouse.parentElement.parentElement.classList.add('is-disabled');
  } else {
    fieldSpouse.disabled = false;
    fieldSpouse.parentElement.parentElement.classList.remove('is-disabled');
  }

  //     
  if ( !window.selectChildren ) {
    initSelectChildren(currentForm, currentMember, currentMember.m_age, currentMember.m_sib_id);
  }

  currentForm.classList.add('is-show');

}
