import Form from './Form';

export default class Household extends Form {
  constructor(arg = {}) {
    super();
    this.h_id = arg.h_id || null; //  id 
    this.h_name				= arg.h_name || null; //   (, "")
    this.h_room_id = arg.h_room_id || null; //   id   
    this.h_household_number = arg.h_household_number || null; //  
    this.h_household_member_count = arg.h_household_member_count || 1; //   
    this.h_household_rooms = arg.h_household_rooms || null; //     
    this.h_household_room_parts = arg.h_household_room_parts || false; //     
    this.h_ownership_id = arg.h_ownership_id || '99'; //     (   (),    (),  ,  )
    this.h_improve_internet_id = arg.h_improve_internet_id || '99'; //      ( )
    //        this.members_household                = arg.members_household || null //  
    this.h_temp_residents = arg.h_temp_residents || false; //   
    this.members				= []; // 
    this.current_member			= null;
    this.edit = false; 
    this.h_error_class = arg.h_error_class || '';
    this.h_indicators = arg.h_indicators || '';
  }
}
