  // formCreateMember.querySelector('[name="m_bith_forgot"]').checked = false;
  // formCreateMember.querySelector('[name="m_bith_forgot"]').parentElement.parentElement.parentElement.classList.add('is-disabled');
  // formCreateMember.querySelector('[name="m_age"]').parentElement.parentElement.classList.add('is-disabled');
  // formCreateMember.querySelector('[name="m_age"]').disabled = true;

  if ( fieldParent.value === '' ) {
    fieldParent.classList.add('is-empty');
  }

  if ( fieldSpouse.value === '' ) {
    fieldSpouse.classList.add('is-empty');
  }

  if ( fieldConnectionToFirst.value === '' ) {
    fieldConnectionToFirst.classList.add('is-empty');
  }

