import handlerFormMember from './navigation-wizard/handler-form-member';
import handlerFormRoom from './navigation-wizard/handler-form-room';
import sendData from './send-data';

export default function initNavigationWizard() {
  const root = document.querySelector('.js-root');
  const btns = root.querySelectorAll('.js-navigation-wizard-btn');
  const step = 1;


  //     
  btns.forEach(btn => {
    btn.addEventListener('click', function(e) {
      let currentSection = root.getAttribute('data-section');

      //   ( )  ( )
      if ( currentSection === 'room' || currentSection === 's_member' ) {

        let currentTabNumber = root.querySelector(`.${currentSection} li.is-active`).getAttribute('data-info').replace(/[^\/0-9]/g, '');
        let allTabsTitle = root.querySelectorAll(`.${currentSection} li[data-info]`);
        let direction = this.getAttribute('data');

        for ( let i = 0; i < allTabsTitle.length; i++ ) {
          //          
          if ( i === +(currentTabNumber - 1) && direction === 'next' && +currentTabNumber !== allTabsTitle.length ) {

            //    ,    
            if ( allTabsTitle[ i + step ].classList.contains('disabled') ) {

              //     
              for ( let nextStep = i + step; nextStep < allTabsTitle.length; nextStep++ ) {

                if ( !allTabsTitle[nextStep].classList.contains('disabled') ) {
                  allTabsTitle[nextStep].firstChild.click();
                  break;
                }
                if ( nextStep === 19 ) {
                  handlerFormMember();
                }
                
              }
            } else {
              //    ,     
              allTabsTitle[ i + step ].firstChild.click();
            }

            //          
          } else if ( i === +(currentTabNumber - 1) && direction === 'back' && +currentTabNumber !== 1 ) {

            //    ,    
            if ( allTabsTitle[ i - step ].classList.contains('disabled') ) {

              //     
              for ( let nextStep = i - step; nextStep < allTabsTitle.length; nextStep-- ) {
                
                if ( !allTabsTitle[nextStep].classList.contains('disabled') ) {
                  allTabsTitle[nextStep].firstChild.click();
                  break;
                } else if ( nextStep === 0 ) {
                  console.log(allTabsTitle);
                  console.log(nextStep);
                  console.log(allTabsTitle[nextStep].classList.contains('disabled'));
                  console.log(!allTabsTitle[nextStep].classList.contains('disabled'));
                  handlerFormMember();
                  break;
                }
              }
            } else {
              //    ,     
              allTabsTitle[ i - step ].firstChild.click();
            }
            
            //      ()   ,   
          } else if ( currentSection === 'room' && direction === 'back' && +currentTabNumber === 1 ) {
            if ( document.body.classList.contains('android') ) {
              document.querySelector('.js-back-to-all-rooms').click();
            } else {
              handlerFormRoom(direction);
            }
            break;

            //      ()   ,    
          } else if ( currentSection === 'room' && direction === 'next' && +currentTabNumber === allTabsTitle.length ) {
            handlerFormRoom(direction);
            break;

            //      ()     ()   ,    
          } else if ( currentSection === 's_member' && direction === 'back' && +currentTabNumber === 1 || 
                      currentSection === 's_member' && direction === 'next' && +currentTabNumber === allTabsTitle.length ) {
            handlerFormMember();
            break;
          }

        }

        //   family ( ,  )
      } else if ( currentSection === 'family' ) {

        let currentTab = root.querySelector('.family li.is-active[data]').getAttribute('data');
        let currentTabLeftNumber = root.querySelector('.family li.is-active[data-household]').getAttribute('data-household');
        let allTabsTitleTop = root.querySelectorAll('.family li[data]');
        let allTabsTitleLeft = root.querySelectorAll('.family li[data-household]');
        let direction = this.getAttribute('data');

        if ( direction === 'next' && currentTab === 'household' ) {

          //  ( )
          allTabsTitleTop[1].firstChild.click();

        } else if ( direction === 'next' && currentTab === 'tree' && +currentTabLeftNumber !== allTabsTitleLeft.length ) {

          let nextTabHoushold = root.querySelector(`.family li[data-household="${+currentTabLeftNumber + 1}"]`).firstChild;
          nextTabHoushold.click();

          //  ( )
          allTabsTitleTop[0].firstChild.click();
          
        } else if ( direction === 'next' && currentTab === 'tree' && +currentTabLeftNumber === allTabsTitleLeft.length ) {
          sendData();
        }

        if ( direction === 'back' && currentTab === 'tree' ) {

          //  ( )
          allTabsTitleTop[0].firstChild.click();

        } else if ( direction === 'back' && currentTab === 'household' && +currentTabLeftNumber !== 1 ) {

          let nextTabHoushold = root.querySelector(`.family li[data-household="${+currentTabLeftNumber - 1}"]`).firstChild;
          nextTabHoushold.click();
          
          //  ( )
          allTabsTitleTop[1].firstChild.click();

        } else if ( direction === 'back' && currentTab === 'household' && +currentTabLeftNumber === 1 ) {

          const btnRoom = root.querySelector('.js-btn-room');
          btnRoom.click();
        }

      } else {
        console.log(currentSection);
      }
      
      document.querySelector('html').scroll(0, 0);
    });
  });
}
